.header {
    &__content{
        max-width: 1052px;
        padding: 0px 10px;
        margin: 0 auto;

        @media (max-width: $md3) {
            display: flex;
            justify-content: right;
            margin: 0;
        }
    }
}

.menu{
    &__body{
        @media (max-width: $md3) {
            display: none;
            //position: fixed;
            width:100%;
            height: 100%;
            top: 0;
            left: 0;
            overflow: auto;
        }
    }

    &__list{
        height: 82px;
        padding: 10px 0;
        align-items: center;
        display: flex;
        justify-content: flex-end;
        li{
            margin: 0px 0px 0px 33px;   
        }
        @media (max-width: $md3){
            li{
                margin: 0px 0px 0px 33px; 
            }

        }
    }
    
    &__link{
        font-size: 20px;
        font-weight: 500;
        color: #000;
        transition: color 0.3s ease 0s;

        &._active{
            color: #ff6464
        }
        @media (min-width: $md2) {
            &:hover{
                color:#ff6464;
            }         
        }
    }
}

.icon-menu{
    display: none;
    @media (max-width: $md3){
        display: block;
        
        position: relative;
        text-align: right;
        margin: 20px 0px 0px 0px;
        
        width: 30px;
        height: 18px;
        cursor: pointer;
        z-index: 5;
        span{
            transition: all 0.3s ease 0s;
            top: calc(50% - 1px);
            right: 0px;
            position: absolute;
            width: 100%;
            height: 2px;
            background-color: #000;
            &:first-child{
                top: 0px;
            }
            &:last-child{
                top: auto;
                bottom: 0px;
            }
        }
    }
}

.overlay{
    &__container{
        height: 0%;
        width: 100%;
        position: fixed;
        z-index: 1;
        top: 0;
        background-color: rgba(0,0,0, 0.9);
        overflow-x: hidden;
        transition: 0.5s;
    }
    &__button{
        position: relative;
        top: 10%;
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    &__content{
        position: relative;
        top: 25%;
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    &__link{
        font-size: 30px;
        line-height: 50 / 30 * 100%;
        font-weight: 500;
        color: #fff;
        &._active{
            color: #ff6464
        }
        &._closebtn{
            font-size: 60px;
        }
    }
}