.hello{
    padding: 38px 0px 71px 0px;
    @media (max-width: $md1) {
        padding: 0px 0px 70px 0px;
        
    }
    @media (max-width: $md3) {
        padding: 0px 0px 60px 0px;
        
    }
    &__container{
        display: flex;
        @media (max-width: $md3) {
            align-items: center;
            text-align: center;
            flex-direction: column-reverse;
            
        }


    }
    &__content{
        align-items: flex-start;
        flex: 1 1 auto;
        padding: 18px 110px 0px 0px;
        *:last-child{
            margin-bottom: 0;
        }
        @media (max-width: $md1) {
            padding: 18px 50px 0px 0px;
            
        }
        @media (max-width:$md3){
            padding: 0px 0px 0px 0px;
        }

    }
    &__title{
        margin: 0px 0px 25px 0px;
        @media (max-width: $md3) {
            margin: 0px 0px 20px 0px;
            
        }
    }
    &__text{
        margin: 0px 0px 35px 0px;
        @media (max-width: $md3) {
            margin: 0px 0px 25px 0px;
            
        }
    }
    &__btn{
        margin: 0px 0px 25px 0px;
        @media (max-width: $md3) {
            margin: 0px 0px 20px 0px;
            
        }

    }
    &__avatar{
        flex: 0 0 243px;
        height: 243px;
        width: 243px;
        border-radius: 50%;
        overflow: hidden;
        box-shadow: -5px 13px 0px 0px #edf7fa;
        img {       
            max-width: 100%;        
        }
        @media (max-width: $md3) {
            flex: 0 0 170px;
            width: 170px; 
            height: 170px;
            margin: 0px 0px 40px 0px;
            
        }


    }

}

.recent-posts {
    background: #edf7fa;
    padding: 25px 0px 13px 0px;

    @media (max-width: $md3){
        padding: 25px 0px 23px 0px;
    }

    &__header{
        display: flex;
        margin: 0px 0px 20px 0px;
    }

    &__title{
        padding: 0px 20px 0px 0px;
        flex: 1 1 auto;
        font-size: 22px; 
    }

    &__view-all{
        flex: 0 0 auto;
        font-size: 16px;
        line-height: 32px;
        color: #00a8cc;

        @media (max-width: $md3) {
            display: none;
        }
    }

    &__items{
        margin: 0px -10px;

        @media (min-width: $md3){
            display: flex;
        }
    }

    &__column{
        flex: 0 1 50%;
        padding: 0px 10px;
        margin: 0px 0px 17px 0px; 
    }
}

.recent-post{
    height: 90%;
    background-color: #fff;
    box-shadow: 0px 4px 10px rgba(187,225,250,0.25);
    border-radius: 4px;
    padding: 20px;

    &__title{
        display: inline-block;
        color: inherit;
        font-size: 26px;
        font-weight: 700;
        line-height: 38 / 26 * 100%;
        transition: color 0.3s ease 0s;
        margin: 0px 0px 17px 0px;

        @media (min-width: $md3) {
            &:hover{
                color: #ff6464;
            }       
        }

        @media (max-width: $md3){
            font-size: 22px;
        }
    }

    &__info{
        margin: 0px 0px 15px 0px;
        font-size: 18px;
        line-height: 26 / 18 * 100%;

        span{
            margin: 0px 10px;
        }

        @media (max-width: $md3){
            font-size: 16px;
        }
    } 
}

.title-posts{
    font-size: 22px;
  
    @media (max-width: $md3) {
        align-items: center;
        text-align: center;
        font-size: 18px;
        padding: 0;
    }
}

.featured-works{
    padding: 30px 0px 80px 0px;
    @media (max-width:$md3) {
        padding: 30px 0px 30px 0px;    
    }
}

.featured-works__title{
    margin: 0px 0px 20px 0px;
}




